const assertShow = `//    as     <> 

// 1
// 2
// 3 any
// 4 any 
// 5 A  B A  B  B  A 

// numberVar as any as stringVar

// TypeScript 1 Cat 
// 

// 1 as 
interface Cat {
  name: string;
  run(): void;
}
interface Fish {
  name: string;
  swim(): void;
}
function isFish(animal: Cat | Fish) {
  
  if (typeof (animal as Fish).swim === 'function') {
      return true;
  }
  return false;
}

// 2 as any
(window as any).name = 'window';
`;

