const enumShow = `// 
// 12
// constant membercomputed member

// 
enum Color1 {Red, Green, Blue}
console.log(Color1["Red"] === 0); // true
console.log(Color1[1] === "Green"); // true

// 
enum Color2 {Red, Green, Blue = "color-blue".length}
console.log(Color2["Blue"]) // 10

// 
enum Mounths {January, February, March = 3,  April}
console.log(Mounths["January"]); // 0
console.log(Mounths["February"]); // 1
console.log(Mounths["March"]); // 3
console.log(Mounths["April"]); // 4

// const enum
const enum Days1 {Sun, Mon, Tue}
const daysList1 = [Days1.Sun, Days1.Mon, Days1.Tue];
console.log(daysList1); // [0, 1, 2]
console.log(Days1);  // {0: "Sun", 1: "Mon", 2: "Tue", Sun: 0, Mon: 1, Tue: 2}

// declare enum
declare enum Days2 {Sun, Mon, Tue}
const daysList2 = [Days2.Sun, Days2.Mon, Days2.Tue];  // Days2 is not defined

// declare  const 
declare const enum Days3 {Sun, Mon, Tue}
const daysList3 = [Days3.Sun, Days3.Mon, Days3.Tue];  // Days3 is not defined
`;

