const classShow1 = `//  Class
// ES6: constructorextendsgetter & setterstatic
// ES7: 

// TypeScript: publicprivateprotected
// TypeScript  private ;
// private ;
// protected ;
// constructor  private 
// constructor  protected 

class Student {
  public name: string; // 
  private age: number; // 
  protected group: string; // 
  public constructor(name: string, age: number, group: string) {
      this.name = name;
      this.age = age;
      this.group = group;
  }
}

let a = new Student('', 10, '');
a.age = 12; 
a.group = ''; 
console.log(a.age); // 12
console.log(a.group); // 
console.log(a.name); // 
`;

