const classShow3 = `//  Class
// abstract 
// 
// 

abstract class Animal { // abstract 
  public name: string;
  public constructor(name: string) {
      this.name = name;
  }
  public abstract sayHi(): any; // abstract 
}

class Cat extends Animal { // 
  public sayHi() { // 
    console.log('Meow, My name is ' + this.name);
  }
}

let cat = new Cat('Tom');
cat.sayHi(); // Meow, My name is Tom
`;

